body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-color: #645b3e;
  --secondary-color: #063d43;
  --main-text-color: #000;
  --second-text-color: #fff;
  --highlight: #e81e63;
}

input {
-webkit-appearance: none;
-moz-appearance: none;
appearance: none;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.cell-num {
  font-size: 10px;
  line-height: 7px;
  max-height: 7px;
  margin: 0;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #645b3e;
  background-color: var(--main-color);
  min-height: 5vh;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
    margin-bottom: 25px;

}

.App-Content {
  display: flex;
  flex-direction: row;
  color: #000;
  color: var(--main-text-color);
}

.App-link {
  color: #61dafb;
}


.line {
   left: 10px;
   margin: 4px auto;
   height: 3px;
   width: 20px;
   background: #fff;
   border-radius: 2px;
   display: block;
}


.Canvas {
  min-width: 60%;
  max-width: 100%;
  text-align: center;
  font-size: calc(10px + 1vmin);
}

.Canvas.full-width {
  min-width: unset;
  width: 100%;
  margin: 0 50px;
}

.circle-picker {
  margin: 0 auto !important;
}

.Separator {
  max-width: 90%;
  margin: 20px auto;
  border-color: #645b3e;
  border-color: var(--main-color);
}

table {
  border-spacing: 0;
  background-repeat: no-repeat;
  background-size: auto 90%;
  background-position-x: 2.3em;
  background-position-y: 1em;
}

td, tr {
  width: 20px;
  height: 20px;
  max-width: 20px;
  max-height: 20px;
  min-width: 20px;
  min-height: 20px;
  border: 1px solid #282c34;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.no-border {
  border: none;
}

/* MOBILE STYLES */
@media screen and (max-width: 600px) {
  .App-header {
    z-index: 2;
    position: relative;
    margin-bottom: 25px;
  }

  .App-Content {
    flex-direction: column;
  }

  .App-Settings,
  .Canvas {
    width: 100%;
    padding: 0;
  }

  .Canvas {
    text-align: center;
  }

  .Canvas.full-width {
    margin: 0;
  }

}

.App-Settings {
	width: 100%;
	font-size: calc(10px + 1vmin);
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	/* border-right: 1px solid var(--main-color); */
}

#sizePicker {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
}

.form-buttons {
	padding-bottom: 10px;
	display: flex;
	flex-direction: column;
	justify-content:space-around;
	margin-right: 15px;
}

input {
-webkit-appearance: none;
-moz-appearance: none;
appearance: none;
}

.utility-button {
	background-color: var(--secondary-color);
}

label {
  padding-left: 15px;
  max-height: 44px;
  border-radius: 5px;
  border-bottom: 2px solid black;
  color: #000;
  color: var(--second-text-color);
  background-color: var(--secondary-color);
}

#input_height,
#input_width {
	padding: 10px 15px;
	border-radius: 5px;
	border: none;
	max-width: 50px;
	max-height: 44px;
	margin: 5px 15px;
	margin-right: 2px;
	font-size: 0.8em;
	color: var(--second-text-color);
	background-color: var(--secondary-color);
}

.input_submit {
	padding: 10px 15px;
	border-radius: 5px;
	border: none;
	font-size: 0.9em;
	background-color: var(--secondary-color);
	color: var(--second-text-color);
	border-bottom: 2px solid black;
}

.input_submit:hover {
  opacity: 0.9;
  cursor: pointer;
}

.colorPicker-cell {
	max-height: 50px;
	max-width: 50px;
	height: 50px;
	width: 50px;
	margin-right: 5px;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid black;
	border-radius: 5px;
	cursor: pointer;
}

.color-edit {
	border: 1px solid red;
}

.color-edit-selected {
	border: 2px solid black;
}

.colorPicker-cell p {
	color: black;
}

.colorPicker-row {
	display: flex;
	flex-direction: row;
	margin-left: 25px;
}

.color-selected {
	border: 2px solid rgb(255, 11, 226);

}

.sketch {
	position: absolute;
	margin-top: 10px;
}
